import React, { Suspense, lazy } from 'react';
const Time = lazy(() => import( "../../Elements/Time/Time"));
const Network = lazy(() => import( "../../Elements/Network/Network"));
const Author = lazy(() => import( "../../Elements/Author/Author"));
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font, rating, ownerId, instaHash }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  const networkIcon = (network.id !== null && network.id !== '') ? true : false
  return <div className="tb_hc_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    {postAuthor ? <Suspense fallback={<></>}><Author network={network} author={author} authorClass={'tb_hc_author_profile'} /></Suspense> : ''}
    <div className="tb_hc_author_info">
      {postAuthor ? <div className={`tb_hc_authorname tb-cTBfont-${ThemeRule.font_varient}`}>{author.name}</div> : ''}
      {<div className="tb_hc_post_info">
        {postAuthor ? <div className="tb_hc_username">{username}</div> : ''}
        {authorSeprator ? <div className="tb_hc_seprator"> </div> : ''}
        {personalization.postTime ? <Suspense fallback={<></>}><Time postTime={postTime} timeClass={`tb_hc_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /></Suspense> : ''}
      </div>}
    </div>
    {networkIcon ?
      <div className="tb_hc_social_">
        <Suspense fallback={<></>}><Network networkClass={'tb_hc_social_ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /></Suspense>
      </div>
      : null}
  </div>
}
export default AuthorDetails